/**
 * Single Product
 */
.single-product .product {
	position: relative;
}

.woocommerce-product-gallery {
	position: relative;

	.woocommerce-product-gallery__trigger {
		position: absolute;
		top: 1em;
		right: 1em;
		display: block;
		z-index: 99;
	}

	.flex-viewport {
		margin-bottom: 30px;
	}

	.flex-control-thumbs {
		@include clearfix;
		margin: 0;
		padding: 0;

		li {
			list-style: none;
			cursor: pointer;
			float: left;

			img {
				opacity: .5;

				&.flex-active {
					opacity: 1;
				}
			}

			&:hover {
				img {
					opacity: 1;
				}
			}
		}
	}

	@for $i from 2 through 5 {
		&.woocommerce-product-gallery--columns-#{$i} {
			.flex-control-thumbs {
				li {
					@include column-width($i);

					&:nth-child(#{$i}n) {
						margin-right: 0;
					}

					&:nth-child(#{$i}n+1) {
						clear: both;
					}
				}
			}
		}
	}
}

.stock {
	&:empty::before {
		display: none;
	}

	&.in-stock {
		color: $woo-color-success;
	}

	&.out-of-stock {
		color: $woo-color-error;
	}
}
