.error404 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  overflow: hidden;
  background-color: #fff;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
}

.error404::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.7);
}

.error404 .site-header,
.error404 .site-footer {
  display: none;
}

.s-404__logo {
  display: inline-block;
  animation: puff-in-middle 0.35s cubic-bezier(0.47, 0, 0.745, 0.715) both 1.5s;
}

.s-404__logo:hover img {
  transform: scale(1.05);
}

.s-404__logo img {
  width: 180px;
  transition: transform 0.2s ease-in-out;
}

.s-404__title {
  animation: puff-in-middle 0.35s cubic-bezier(0.47, 0, 0.745, 0.715) both 0.5s;
}

.s-404__title span {
  font-size: 250px;
  line-height: 1;
  color: #000;
  display: inline-block;
  position: relative;
  margin: 0 10px;
}

@media screen and (max-width: 991px) {
  .s-404__title span {
    font-size: 200px;
  }
}

@media screen and (max-width: 575px) {
  .s-404__title span {
    font-size: 125px;
  }
}

.s-404__title-1 {
  transform: rotate(-7deg);
  animation: 5s float ease-in-out infinite alternate-reverse 0.5s;
}

.s-404__title-2 {
  transform: translateY(10%);
  animation: 5s float-2 ease-in-out infinite alternate-reverse 0.5s;
}

.s-404__title-3 {
  transform: rotate(10deg);
  animation: 5s float-3 ease-in-out infinite alternate-reverse 0.5s;
}

.s-404__title-icon {
  font-size: 62px;
  color: #000;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@media screen and (max-width: 991px) {
  .s-404__title-icon {
    font-size: 55px;
  }
}

@media screen and (max-width: 575px) {
  .s-404__title-icon {
    font-size: 40px;
  }
}

.s-404__text {
  font-size: 40px;
  line-height: 1.1;
  color: #000;
  letter-spacing: 1px;
  animation: puff-in-middle 0.35s cubic-bezier(0.47, 0, 0.745, 0.715) both 0.8s;
  margin-top: 40px;
  padding: 0 15px;
}

@media screen and (max-width: 575px) {
  .s-404__text {
    margin-top: 25px;
    font-size: 30px;
  }
}

.s-404__btn {
  font-size: 17px;
  color: #fff;
  background-color: #000;
  text-transform: uppercase;
  display: inline-block;
  position: relative;
  margin-top: 35px;
  padding: 8px 35px;
  min-width: 180px;
  border: 2px solid #000;
  border-radius: 30px;
  transition: 0.2s ease-in-out;
  animation: puff-in-middle 0.35s cubic-bezier(0.47, 0, 0.745, 0.715) both 1s;
}

.s-404__btn:hover {
  color: #000;
  background-color: #fff;
}

@media screen and (max-width: 991px) {
  .s-404__btn {
    font-size: 16px;
    padding: 5px 20px;
    margin-top: 25px;
  }
}

@keyframes puff-in-center {
  0% {
    transform: scale(0.5);
    filter: blur(2px);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    filter: blur(0);
    opacity: 1;
  }
}

@keyframes puff-in-middle {
  0% {
    transform: scale(1.4);
    filter: blur(2px);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    filter: blur(0);
    opacity: 1;
  }
}

@keyframes float {
  0% {
    transform: rotate(-1deg) translate(0, 0);
  }
  100% {
    transform: rotate(-7deg) translate(10%, 10%);
  }
}

@keyframes float-2 {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10%);
  }
}

@keyframes float-3 {
  0% {
    transform: rotate(-4deg) translateY(0);
  }
  100% {
    transform: rotate(10deg) translateY(-10%);
  }
}

